.container{
	/*composes: card-dimens from '../../shared/styles.css';*/
	margin-bottom: 4%;
	margin-right: 5%;
	/*material design card shadow effect*/
	-webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
	        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
	-webkit-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
	-o-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
	transition: all 0.3s cubic-bezier(.25,.8,.25,1);
	height: 100%;
}

.container:hover{
	-webkit-box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
	        box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.imageContainer{
	height: 400px;
}

.container-leave{
	opacity: 1;
}

.container-leave .container-leave-active{
	opacity: 0.01;
   -webkit-transition: opacity 500ms ease-in;
   -o-transition: opacity 500ms ease-in;
   transition: opacity 500ms ease-in;
}

.author{
	font-weight: bold;
}
.author:hover{
	text-decoration: underline;
	cursor: pointer;
}

.bottomBar{
	-ms-flex-pack: justify;
	    justify-content: space-between;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	position: absolute;
	background-color: rgba(0,0,0,0.6);
	bottom: 0;
	padding-top: 4%;
	width: 100%;
}

.btnDelete{
	background: url('../../images/delete-32-white.png') no-repeat scroll 0 0 transparent;
	width: 32px;
	height: 32px;
	cursor: pointer;
	border: none;
	-ms-flex-order:2;
	    order:2;
	margin-right: 10%;
	padding-bottom: 20%;
	-ms-flex-item-align: end;
	    align-self: flex-end;
}
.bottomText{
	-ms-flex-order: 1;
	    order: 1;
	font-size: 1.1em;
	margin-left: 10%;
	margin-bottom: 2%;
}


.btnDelete:hover{
	background: url('../../images/delete-32.png') no-repeat scroll 0 0 transparent;
}



